'use strict';

class StringBuilder {
  constructor(baseString = '') {
    this.value = String(baseString); //    String,        
    //    ,        ,
    //    ,          .
  }
  append(str) {
    this.value = this.value + str;
    return this; //     this(),       ,
    //            builder.prepend(); builder.pad();.
  };
  prepend(str) {
    this.value = str + this.value;
    return this;
  };
  pad(str) {
    this.value = str + this.value + str;
    return this;
  }
}

export default StringBuilder

