'use strict';
import template from '../../templates/template.hbs';
import * as basicLightbox from 'basicLightbox';
import apiService from "./apiService.js";
import '../../style/style.css';
import '../../../node_modules/basiclightbox/dist/basicLightbox.min.css';

const input = document.querySelector('input[name = "query"]'); //  DOM  input c  querySelector   input   name.
const gallery = document.querySelector('.js-gallery'); //  DOM  ul c  querySelector   className (js-gallery).
const target = document.querySelector('.js-target'); //  DOM  div c  querySelector   className (js-target).
const body = document.body; //  DOM  div c  querySelector   className (js-target).
const key = '16360208-b38b3071e12220d724142a6b1'; //    Pixabay API.
const numberOfResults = 20; //        Pixabay. (         Pixabay   20).
const options = { //    IntersectionObserver.
  root: null,
  rootMargin: '300px',
  threshold: 1
};
const observer = new IntersectionObserver(updateList, options) //   IntersectionObserver     callback   .
let lastSelectedElement = null;
let numberOfPage;
let searchQuery;
let instance;

gallery.addEventListener('click', openModal);

function searchImg() {
  gallery.innerHTML = ''; //   DOM     ,     . 
  searchQuery = input.value; //    c input.
  observer.unobserve(target)  //      .
  numberOfPage = 1; //   ,     .
  if (searchQuery !== '') { //         input.
    apiService(key, searchQuery, numberOfResults, numberOfPage) //       Pixabay   .
      .then(data => { //    Pixabay       DOM   
        gallery.insertAdjacentHTML('beforeend', template(data));
        observer.observe(target); //  observer      .
      });
  };
}

function openModal(event) {
  (lastSelectedElement !== null) ? lastSelectedElement.removeAttribute('select'): false; //        null         ,      Pixabay API
  body.removeEventListener('keydown', controlModal); //     body.
  const isIMG = event.target.tagName === 'IMG'; //              img.
  if (isIMG) {
    const currentImg = event.target;//      .
    body.addEventListener('keydown', controlModal); //   body       basicLightbox.
    createModal(currentImg);
  };
}

function createModal(item) {
  item.setAttribute('select', true); //     -.
  lastSelectedElement = item;//      .
  instance = basicLightbox.create(`<img src="${item.srcset}" width="800" height="600">`);//         srcset  . 
  return instance.show(); //      show     .
}

function controlModal(event) {
  const allImg = document.querySelectorAll('.js-img');//  querySelectorAll      (js-img).
  const escape = event.keyCode === 27;//     boolean        escape.
  const arrowRight = event.keyCode === 39;//     boolean        Arrow right.
  const arrowLeft = event.keyCode === 37;//     boolean        Arrow left.
  const isOpenModal = instance.visible()//     boolean     .
  if (escape) {
    instance.close();//      close      .
    body.removeEventListener('keydown', controlModal); //    body.
  };
  if (isOpenModal) {
    if (arrowRight) {
      const currentIdx = Array.from(allImg).findIndex(elementSelected);//         .
      const nextIdx = currentIdx + 1; //      1    .
      (nextIdx < allImg.length) ? createModal(allImg[nextIdx]): false; //                     .
    };
    if (arrowLeft) {
      const currentIdx = Array.from(allImg).findIndex(elementSelected);//        
      const prevIdx = currentIdx - 1;//     1     .
      (prevIdx > -1) ? createModal(allImg[prevIdx]): false;//       -1           .
    };
  } else {
    body.removeEventListener('keydown', controlModal); //    body.
  };
}

function elementSelected(element) {
  const isSelected = element.hasAttribute('select');//       bollean       select.
  if (isSelected) {
    element.removeAttribute('select');//     select.
    instance.close();//   .
    return element;
  }
}

function updateList(entries) {
  console.log(entries);
  entries.forEach(entry => {//    IntersectionObserver.
    if (entry.isIntersecting) {//    isIntersecting  = true,   .
      numberOfPage += 1;//   1    .
      apiService(key, searchQuery, numberOfResults, numberOfPage).then(data =>
        gallery.insertAdjacentHTML('beforeend', template(data))
      );
    };
  });
}

export default searchImg;