const input = document.querySelector('.js-input'); //  DOM  input c  querySelector   className (js-input).
const buttonCreate = document.querySelector('[data-action="create"]') //  DOM  button c  querySelector   data-attribute (create).
const container = document.querySelector('#boxes'); //  DOM  div c  querySelector   ID (boxes).

let boxesArray = []; //   ,      div .
let elementDivSize = 20; //         div      ,            .

function handlerCreateBoxes(event) {
  const enter = event.keyCode === 13; //   boolean           Enter.
  const clickCreate = event.target === buttonCreate; //   boolean , true  evnt      .
  if (enter || clickCreate) { //       .     Enter     .
    const currentValue = Number(input.value); //          (    ),      .
    createBoxes(currentValue);
    input.value = ''; //  .
  }
}

function handlerDestroyBoxes() {
  destroyBoxes();
  input.value = '';
}

function createBoxes(ammount) {
  for (let i = 0; i < ammount; i += 1) { //            ammount
    const div = createDiv(); //       .
    boxesArray.push(div); //  push       div.
  }
  createMarkup(boxesArray);
}

function destroyBoxes() {
  container.innerHTML = ''; //      .
  boxesArray.splice(0, boxesArray.length); //  .
  elementDivSize = 20; //   .
}

function createDiv() {
  const elementDiv = document.createElement('div'); //    div
  elementDivSize += 10; //           10
  elementDiv.style.width = elementDivSize + 'px'; //           .
  elementDiv.style.height = elementDivSize + 'px'; //           .
  elementDiv.style.backgroundColor = getRandomRGBColor();
  return elementDiv;
}

function createMarkup(arr) {
  const fragment = document.createDocumentFragment(); //  DocumentFragment.
  arr.forEach(element => { //           DocumentFragment.
    fragment.append(element); // T  DocumentFragment     DOM ,        reflow.
  });
  container.append(fragment); //   DocumentFragment  DOM       .
}

function getRandomRGBColor() {
  const r = getRandomNumber();
  const g = getRandomNumber();
  const b = getRandomNumber();
  const rgb = `rgb(${r},${g},${b})`; //        RGB     .
  return rgb;
}

function getRandomNumber() {
  const randomNumber = Math.floor(Math.random() * 256); //     0  255.
  return randomNumber;
}

export {
  handlerCreateBoxes,
  handlerDestroyBoxes
};